@import "../../constants.scss";

.toggle-menu {
  background-color: rgba(254, 205, 169, 0.7);
  overflow: hidden;
  position: fixed;
  top: -250px;
  left: -470px;
  height: 800px;
  width: 800px;
  z-index: 990;
  border-radius: 50%;
  animation: fade-in-background 0.6s;
  transition: all cubic-bezier(0.34, 1.56, 0.64, 1) 1s;
  box-shadow: 0px 0.5rem 2rem rgba(44, 68, 106, 0.3);

  .navigation {
    &_container {
      animation: showing-left 1.1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards;
      position: fixed;
      top: 50px;
      left: -50px;
      a {
        margin-left: 100px;
        font-size: 30px;
        line-height: 40px;
        color: $darkcoral-color;
      }
      @include breakpoint(medium) {
        top: 115px;
      }
      @include breakpoint(small) {
        top: 80px;
      }
    }
  }
}
.hidden {
  display: none;
}

.toggle-button {
    border: none;
    background-color: transparent;
    position: fixed;
    top: 50px;
    left: 50px;
    z-index: 999;
    &:focus {
      outline: transparent;
    }
    svg {
      color: $white-color;
      color: pink;
      font-size: 50px;
    }
  
    &__icon {
      display: none;
  
      @include breakpoint(medium) {
        display: block;
      }
    }
    @include breakpoint(small) {
      top: 50px;
      left: 20px;
      svg {
        font-size: 40px;
      }
    }
  }