@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    opacity: 1;
    transform: translateY(0);
  }
  60% {
    transform: translateY(-500px);
  }
  40% {
    transform: translateY(-700px);
  }
  0%{
    opacity: 1;
    transform: translateY(-1000px);
  }
}

/*********************
Mixins
*********************/
@mixin position($top-value, $left-value) {
  position: absolute;
  top: $top-value;
  left: $left-value;
}
