@import "../../constants.scss";
.contactForm {
    background-color: rgba(255,192,203, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    max-width: 600px;
    min-height: 400px;
    padding: 20px 0;

    @include breakpoint(small) {
      margin: 0 30px;
    }

    & label {
      display: block;
      padding: 20px 0 5px;
      width: 66%;
      font-size: .8rem;
    }

    &_name,
    &_email,
    &_message {
      display: block;
      padding: 5px 10px 5px;
      min-width: 66%;
      max-width: 66%;
      border: 2px solid transparent;
      border-bottom: 2px solid $darkcoral-color;
      background: none;
      font-size: 1rem;
      font-family: Monospace;

      &:focus{
        background: $pink-color;
        outline: none; 
      }
    }

    &_message {
      border-color: $darkcoral-color;
    }

    .error {
      color: red;
      font-size: .8rem;
    }
    
    &_button {
      margin: 30px 0;
      padding: 10px 30px;
      border: 2px solid $darkcoral-color;
      color: $darkcoral-color;
      background: none;
      text-transform: uppercase;
      font-weight: 700;
      letter-spacing: 1px;
      transition: .3s;
      cursor: pointer;

      &:hover{
        background: $darkcoral-color;
        color: $pink-color;

      }
    }
    
  }