@import "../constants.scss";


.content {
    background-image: url('../images/background.jpeg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0px -400px;
    position: relative;
    width: 100%;
    min-height: 100%;
    overflow: hidden; 

    @include breakpoint(medium) {
        background-position: center;
    }

    &_logo {
        @include position (5%, 3%);
        height: 400px;
        width: auto;
        z-index: 100;

        @include breakpoint(small) {
            height: 260px;
            left: 1%;
        }

        &:after {
            content: "";
            @include position(5%, 3%);
            background: $darkcoral-color;
            height: 350px;
            width: 350px;
            z-index: -1;
            border-radius: 50%;
            opacity: 0;
            transition: 0.8s cubic-bezier(0.34, 1.56, 0.64, 1) 0.1s;

            @include breakpoint(medium) {
                display: none;
            }
        }

        transition: 0.6s ease-in-out;

        &:hover {
            transform: scale(1.15);

            &:after {
                transform: scale(1.8);
                opacity: 0.5;
            }
        }

        img {
            width: auto;
            height: 100%;
            // opacity: 0;
            animation: showingcircle 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards;
        }
    }

    &_greencircle {
        @include position (25%, 24%);
        height: 320px;
        width: auto;
        z-index: 50;

        @include breakpoint(small) {
            height: 220px;
            @include position (12%, 31%);
        }

        &:after {
            content: "";
            @include position(3%, 3%);
            background: $green-color;
            height: 300px;
            width: 300px;
            z-index: -1;
            border-radius: 50%;
            opacity: 0;
            transition: 0.4s cubic-bezier(0.34, 1.56, 0.64, 1) 0.1s;

            @include breakpoint(medium) {
                display: none;
            }
        }

        transition: 0.6s ease-in-out;

        &:hover {
            transform: scale(1.4);

            &:after {
                transform: scale(1.3);
                opacity: 0.5;
            }
        }

        img {
            opacity: 0;
            animation: showingcircle 0.8s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards 0.4s;
            transition: all 1s;
        }
    }

    &_wheatcircle {
        @include position (50%, 45%);
        height: 250px;
        width: auto;
        // z-index: 40;
        @include breakpoint(small) {
            height: 200px;
            @include position (40%, 6%);
        }
        &:after {
            content: "";
            @include position(0, 0);
            background: $wheat-color;
            height: 250px;
            width: 250px;
            z-index: -1;
            border-radius: 50%;
            opacity: 0;
            transition: 0.4s cubic-bezier(0.34, 1.56, 0.64, 1) 0.1s;
            @include breakpoint(medium) {
              display: none;
            }
          }
          transition: 0.6s ease-in-out;
          &:hover {
            transform: scale(1.4);
            &:after {
              transform: scale(1.2);
              opacity: 0.5;
            }
          }
      
          img {
            opacity: 0;
            animation: showingcircle 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards 0.6s;
            transition: all 1.5s;
          }
    }

    &_redcircle {
        @include position (50%, 65%);
        height: 170px;
        width: auto;
        // z-index: 3;


        @include breakpoint(small) {
            @include position (50%, 44%);
        }
        &:after {
            content: "";
            @include position(0, 0);
            background: $darkcoral-color;
            height: 170px;
            width: 170px;
            z-index: -1;
            border-radius: 50%;
            opacity: 0;
            transition: 0.4s cubic-bezier(0.34, 1.56, 0.64, 1) 0.1s;
            @include breakpoint(medium) {
              display: none;
            }
          }
          transition: 0.6s ease-in-out;
          &:hover {
            transform: scale(1.5);
            &:after {
              transform: scale(1.2);
              opacity: 0.5;
            }
          }
      
          img {
            opacity: 0;
            animation: showingcircle 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards 1s;
            transition: all 1s;
          }
    }

    &_whitecircle {
        position: absolute;
        bottom: 2%;
        right: 5%;
        width: auto;
        z-index: 20;

        @include breakpoint(small) {
            right: 3%;
            bottom: 3%;
        }

        &:after {
            content: "";
            position: absolute;
            bottom: 0%;
            right: -7%;
            background: $white-color;
            height: 100px;
            width: 100px;
            z-index: -1;
            border-radius: 50%;
            opacity: 0;
            transition: 0.4s cubic-bezier(0.34, 1.56, 0.64, 1) 0.1s;
            @include breakpoint(medium) {
              display: none;
            }
          }
          transition: 0.6s ease-in-out;
          &:hover {
            transform: scale(1.6);
            &:after {
              transform: scale(1.05);
              opacity: 0.5;
            }
          }
          img {
            opacity: 0;
            animation: bounce 4s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards 1s;
            transition: all 4s;
          }
    }

    &_circle {

      img{
        width: auto;
        height: 100%;

      }    
    }

}

.animation__welcome{
  @include position(250px, 450px);
    font-size: 1.5rem;
    font-weight: 600;
    color: $blue-color;
    letter-spacing: 5px;
    text-shadow: 0px 0.5rem 2rem rgba(44, 68, 106, 0.7);
    z-index: 200;
    opacity: 0;
    animation: showing-left 0.8s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards 0.6s;

    @include breakpoint(medium){
      font-size: 1.2rem;

    }

    @include breakpoint(small){
      @include position(250px, 250px);

    }
}