@import "../../constants.scss";

.follow-cursor {
  position: absolute;
  width: 50px;
  height: 50px;
  border: 2px solid;
  color: $white-color;
  box-sizing: border-box;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  pointer-events: none;
  z-index: 990;
  transition: all 0.2s ease-out;

  &.active {
    z-index: 999;
    transform-origin: 70% 70%;
    transform: scale(3);
    background-color: $white-color;
    opacity: 0.4;
  }

  @include breakpoint(medium) {
    display: none;
  }
}

.coral-color-cursor {
  color: $darkcoral-color;

  &.active {
    background-color: $darkcoral-color;
  }
}

.blue-color-cursor {
  color: $blue-color;

  &.active {
    background-color: $blue-color;
  }
}