@import "../../constants.scss";

.navigationvisable {
  height: 100vh;
  min-width: 500px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  z-index: 100;
  margin-right: 1%;
  animation: showing-right 1.1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards;

  @include breakpoint(medium) {
    a {
      font-size: 40px;
      line-height: normal;
    }
  }

  @include breakpoint(small) {
    justify-content: center;
    align-items: flex-end;
    min-height: 550px;
    min-width: 100%;
    animation: showing-up 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards;

    a {
      font-size: 40px;
      text-align: center;
      background-color: rgba($color: white, $alpha: 0.4);
      z-index: 10000;

    }
  }
}

a {
  position: relative;
  text-shadow: 0px 0.5rem 2rem rgba(44, 68, 106, 0.2);
  font-size: 60px;
  line-height: 80px;
  display: block;
  text-decoration: none;
  color: $darkcoral-color;
  font-weight: 600;
  padding: 10px;
  z-index: 99999;
  transition: all cubic-bezier(0.34, 1.56, 0.64, 1) 0.6s;
  margin: 10px;
  border-radius: 15px;
  text-align: center;

  &:hover {
    transform: scale(1.2);
  }
}