@import "../../constants.scss";
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;700&family=Sriracha&display=swap');

.tastes_container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-wrap: wrap;

    figure {
        border-radius: 50%;
        height: 300px;
        width: 300px;
        position: relative;
        overflow: hidden;
        opacity: 1;
        transition: 0.6s ease-in-out;
        animation: showingcircle 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards;

        img {
            height: 100%;
            position: relative;
        }

        figcaption {
            font-family: 'Dancing Script', cursive;
            position: absolute;
            opacity: 1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            text-align: center;
            border-radius: 5%;
            color: #F6DC40;
            transition: .45s ease-in-out;
            background-color: rgba($color: $blue-color, $alpha: 0.5);

            // #576CBA 
            &:hover {
                opacity: 0;

            }

            div {
                margin-top: 60px;

                h4 {
                    font-family: 'Sriracha', cursive;
                    opacity: 1;
                }

                p {
                    width: 80%;
                    padding-left: 30px;
                    font-size: 1.1rem;
                }
            }
        }
    }
}

.toggle-menu .navigation_container a{
    color: $blue-color;
}