@import "../constants.scss";

.about {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    z-index: -100;
    width: 100wh;
    height: 100vh;

    & video {
        overflow: hidden;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        z-index: -100;
    }

    &_container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;

        @include breakpoint(medium) {
            flex-wrap: wrap;
        }
    }

    &_logo {
        position: absolute;
        opacity: 0.7;
        top: 15%;
        left: 60%;
        height: 460px;
        width: auto;
        z-index: 1;

        @include breakpoint(medium) {
            height: 250px;
            top: 0;
            right: 5%;
            z-index: 100;
        }

        @include breakpoint(small) {
            height: 100px;
            margin: 0;
            padding: 0;

        }

        &:after {
            content: "";
            @include position(21%, 18%);
            background: $darkcoral-color;
            height: 310px;
            width: 310px;
            z-index: -1;
            border-radius: 50%;
            opacity: 0;
            transition: 0.8s cubic-bezier(0.34, 1.56, 0.64, 1) 0.1s;

            @include breakpoint(medium) {
                display: none;
            }
        }

        transition: 0.6s ease-in-out;

        &:hover {
            transform: scale(1.15);
            opacity: 1;

            &:after {
                transform: scale(1.7);
                opacity: 0.5;
            }
        }

        a {
            width: auto;
            height: 100%;
            animation: showingcircle 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards;
        }
    }

    img {
        width: auto;
        height: 100%;
    }

    &_info {
        position: absolute;
        top: 15%;
        left: 7%;
        text-align: center;
        color: $blue-color;
        font-size: 1.2rem;
        max-width: 45%;
        margin: 25px;
        padding: 20px;
        font-weight: 700;
        z-index: 10;
        background-color: rgba(255, 255, 255, 0.5);
        transition: 0.6s ease-in-out;
        animation: showing-left 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards;

        &:hover {
            transform: scale(1.15);
        }

        @include breakpoint(medium) {
            top: 30%;
            max-width: 75%;
        }

        @include breakpoint(small) {
            top: 10%;
            padding-top: 0;
            margin: 0 auto;
            margin-top: 50px;
            width: 95%;
            font-size: 1.05rem;
        }
    }
}