@import "../constants.scss";

body {
    background-image: url('../images/background-tastes.jpg');
    background-size: cover;
    height: 100%;

}

.tastes {
    display: flex;
    justify-content: center;
    flex-direction: column;

    h1 {
        text-align: center;
        color: $blue-color;
        margin-top: 80px;
        font-size: 3rem;

        @include breakpoint(medium) {
            font-size: 2.5rem;
            margin-top: 100px;
        }

        @include breakpoint(small) {
            font-size: 1.5rem;
            margin-top: 100px;
        }
    }
}

.toggle-button {
    svg {
        color: $blue-color;
    }
}


.tastes_logo {
    height: 150px;
    position: fixed;
    top: 0;
    right: 0;
    opacity: 0.9;

    @include breakpoint(small) {
        height: 100px;
        margin: 0;
        padding: 0;
    }

    a {
        height: 100%;

        img {
            height: 100%;
        }
    }

}