@import "../constants.scss";

.contact {
  background-image: url('../images/contact-background.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0px -400px;
  position: relative;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;

  @include breakpoint(medium) {
    background-position: center;
  }

  &_container {

    margin-top: 50px;

    @include breakpoint(medium) {
      margin-top: 170px;
    }

    @include breakpoint(small) {
      margin-top: 140px;
    }

  }

  &_socialmedia {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 5rem;
    font-size: 3rem;
    margin: 30px 0;



    & h1 {
      font-size: 2rem;
      color: $blue-color;
      text-align: center;

      @include breakpoint(small) {
        font-size: 1.5rem;
      }
    }

    & a {
      color: $blue-color;
      transition: color .3s ease-in-out;
      padding: 50px;
      z-index: 1;

      @include breakpoint(small) {
        padding: 0;
      }

      &:hover {
        transform: scale(1.2);
        transition: all ease-in 0.3s;

      }
    }

  }

  &_form-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &_logo {
    height: 150px;
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0.9;

    @include breakpoint(small) {
      height: 100px;
      margin: 0;
      padding: 0;
    }

    a {
      height: 100%;

      img {
        height: 100%;
      }
    }

  }

}